body,ul,ol,dl,dd,h1,h2,h3,h4,h5,h6,figure,form,fieldset,legend,input,textarea,button,p,blockquote,th,td,pre,xmp{margin:0;padding:0
}
body,input,textarea,button,select,pre,xmp,tt,code,kbd,samp{line-height:1.5;font-family:tahoma,arial,"Hiragino Sans GB",simsun,sans-serif
}
h1,h2,h3,h4,h5,h6,small,big,input,textarea,button,select{font-size:100%
}
h1,h2,h3,h4,h5,h6{font-family:tahoma,arial,"Hiragino Sans GB","\5FAE\8F6F\96C5\9ED1",simsun,sans-serif
}
h1,h2,h3,h4,h5,h6,b,strong{font-weight:normal
}
address,cite,dfn,em,i,optgroup,var{font-style:normal
}
table{border-collapse:collapse;border-spacing:0;text-align:left
}
caption,th{text-align:inherit
}
ul,ol,menu{list-style:none
}
fieldset,img{border:0
}
img,object,input,textarea,button,select{vertical-align:middle
}
article,aside,footer,header,section,nav,figure,figcaption,hgroup,details,menu{display:block
}
audio,canvas,video{display:inline-block;*display:inline;*zoom:1
}
blockquote:before,blockquote:after,q:before,q:after{content:" "
}
textarea{overflow:auto;resize:vertical
}
input,textarea,button,select,a{outline:0 none;border: none;
}
button::-moz-focus-inner,input::-moz-focus-inner{padding:0;border:0
}
mark{background-color:transparent
}
a,ins,s,u,del{text-decoration:none
}
sup,sub{vertical-align:baseline
}
html {overflow-x: hidden;height: 100%;font-size: 50px;-webkit-tap-highlight-color: transparent;
}
body {font-family: Arial, "Microsoft Yahei", "Helvetica Neue", Helvetica, sans-serif;color: #333;font-size: .28em;line-height: 1;-webkit-text-size-adjust: none;
}
hr {height: .02rem;margin: .1rem 0;border: medium none;border-top: .02rem solid #cacaca;
}
a {color: #25a4bb;text-decoration: none;
}
/*引入CSS变量*/
:root {
  /*z-index层级*/
  --content: 0; /*内容层*/
  --nav: 1; /*底部导航层*/
  --header: 2; /*头部层，因为有的页面只有头部，没有底部导航栏，所以将nav和header层做出区分*/
  --mask: 3; /*遮罩层*/
  --popout: 4; /*弹出层，配合遮罩层使用*/

  /*颜色*/
  --soul-background-color: #fff; /* 的背景颜色 */
  --soul-box-shadow-color: #D6D6D6; /* header和nav的阴影颜色 */

  /*模糊距离*/
  --soul-box-shadow-blur: 0.35rem;
}



























/*
使用flex布局
*/
.soul-welcome {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: var(--popout);
}
.soul-welcome-hide {
  opacity: 0;
  visibility: hidden;
}
.soul-welcome img {
  margin: 25%;
  width: 50%;
}

.soul-nav {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1rem;
  background-color: var(--soul-background-color, #fff);
  -webkit-box-shadow:
  0rem
  -0.05rem
  var(--soul-box-shadow-blur, 0.35rem)
  var(--soul-box-shadow-color, #D6D6D6);
          box-shadow:
  0rem
  -0.05rem
  var(--soul-box-shadow-blur, 0.35rem)
  var(--soul-box-shadow-color, #D6D6D6);
  z-index: var(--nav);
}
nav dl {
  width: 20%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
nav dt {
  height: 0.7rem;
}
nav dd {
  line-height: 0.2rem;
  font-size: 0.2rem;
}












































